let LayerCamerasMove = cc.Class({
    extends: cc.Component,

    properties: {

        sceneNodes: [cc.Node],

        vx: 0,
        vy: 0,
        ax: 0.5,
        maxV: 1,

        rotRangeX: 10,
        rotRangeY: 10,

        screenWidth: 0,
        screenHeight: 0,
        designResolutionHeight: 0,
        designResolutionHeight_2: 0,

        game: null
    },

    statics:{
        infos:null
    },

    onLoad: function () {
        this.screenWidth = cc.view.getDesignResolutionSize().height / cc.view.getCanvasSize().height * cc.view.getCanvasSize().width;
        this.screenHeight = cc.view.getDesignResolutionSize().width / cc.view.getCanvasSize().width * cc.view.getCanvasSize().height;


        this.designResolutionHeight = cc.view.getDesignResolutionSize().height;
        this.designResolutionHeight_2 = cc.view.getDesignResolutionSize().height / 2;

        LayerCamerasMove.infos = [];
        for (let i = 0; i < this.sceneNodes.length; i++) {
            let info = { x: this.sceneNodes[i].x, y: this.sceneNodes[i].y, z: this.sceneNodes[i].z, height: this.sceneNodes[i].height };
            LayerCamerasMove.infos.push(info);
        }

        if (!cc.sys.isMobile) {
            this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
            this.node.on(cc.Node.EventType.MOUSE_MOVE, this.onMouseMove, this);
        }

    },

    onDestroy: function () {
        this.node.off(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.MOUSE_MOVE, this.onMouseMove, this);
    },

    initialize: function () {
        console.log(window.DeviceMotionEvent);
        if (window.DeviceMotionEvent && typeof window.DeviceMotionEvent.requestPermission === "function") {

            
            window.DeviceMotionEvent.requestPermission();
        }

        // window.addEventListener("devicemotion", this.onDeviceMotion.bind(this));
        window.addEventListener("deviceorientation", this.onDeviceorientation.bind(this));
    },

    onTouchMove: function (evt) {
        // this.onMouseMove(evt);
    },

    onDeviceMotion: function (evt) {
    },

    onDeviceorientation: function (evt) {

        for (let i = 0; i < this.sceneNodes.length; i++) {

            let screenHeight_2 = this.screenHeight / 2;

            let targetX = LayerCamerasMove.infos[i].x + (evt.gamma / 90) * this.rotRangeX * LayerCamerasMove.infos[i].z / 10;

            let targetY = (evt.beta / 90) * this.rotRangeY * (LayerCamerasMove.infos[i].z / 10);

            // let currentX = this.sceneNodes[i].x;

            //let timeX = Math.abs(targetX - currentX) / this.rotRangeX * 2;
            //this.debugNode.string += ('\n'+'time' + i +" : "+ timeX);

            //targetY + 
            // let moveTo = cc.moveTo(1, targetX, this.infos[i].y);


            //this.sceneNodes[i].stopAllActions();
            this.sceneNodes[i].x = targetX;

            if (i == 1) {
                this.sceneNodes[i].height = LayerCamerasMove.infos[i].height + targetY;
            }
            else {
                this.sceneNodes[i].y = LayerCamerasMove.infos[i].y + targetY;
            }
        }
    },


    onMouseMove: function (evt) {
        if (!this.game.furweeIntialized) {
            return;
        }
        // console.log(this.screenWidth/2 - evt._x, this.screenWidth/2);

        for (let i = 0; i < this.sceneNodes.length; i++) {

            let screenWidth_2 = this.screenWidth / 2;
            let screenHeight_2 = this.screenHeight / 2;

            let targetX = (screenWidth_2 - evt._x) / screenWidth_2 * this.rotRangeX * (LayerCamerasMove.infos[i].z / 10);
            let targetY = (evt._y - 370) / screenHeight_2 * this.rotRangeY * (LayerCamerasMove.infos[i].z / 10);




            //let targetMoveX = (this.screenWidth/2 - evt._x)/ (this.screenWidth/2) * this.moveRangeX + this.posX;
            //let targetMoveY = ( evt._y - this.designResolutionHeight_2 ) / this.designResolutionHeight_2 *this.moveRangeY + this.posY;

            // console.log("target", targetX, targetY);
            //let currentX = this.sceneNodes[i].x;
            //let currentY = this.sceneNodes[i].y;

            //console.log('time', timeX, timeY);

            //targetY + 
            //et moveTo = cc.moveTo(timeX, cc.v3(targetX + this.infos[i].x, this.infos[i].y, this.infos[i].z));

            // this.sceneNodes[i].stopAllActions();
            // this.sceneNodes[i].runAction(moveTo);
            this.sceneNodes[i].x = LayerCamerasMove.infos[i].x + targetX;

            if (i == 1) {
                this.sceneNodes[i].height = LayerCamerasMove.infos[i].height + targetY;
            }
            else {
                this.sceneNodes[i].y = LayerCamerasMove.infos[i].y + targetY;
            }


        }

    }
});
