let LayerCamerasMove = require("LayerCamerasMove");
const {Furwee} = require("Furwee");
const {Helper} = require("Helper");

let UIManager = cc.Class({
    extends: cc.Component,

    properties: {
        isHActive: true,

        hNode: cc.Node,
        vNOde: cc.Node,
        uiCamera: cc.Camera,

        musicWidget: cc.Widget,
        musicButton: cc.Node,

        startBlock: cc.Node,

        game: null,
        cameraMove: LayerCamerasMove,
        debugNode: cc.Label,
    },

    statics:{
        instance :null,
        uiBase: null
    },

    onLoad: function(){
        this.startBlock.active = true;


        this.node.on("mobileView", this.mobileView.bind(this));
        this.node.on("pcView", this.pcView.bind(this));

        if (!Helper.isPcView) {
            this.mobileView(Helper.width);

        }
        else{
            this.pcView(Helper.width);
        }

        //update View
       // cc.view.setDesignResolutionSize( cc.Canvas.instance.designResolution.width,  cc.Canvas.instance.designResolution.height, new cc.ResolutionPolicy(cc.ContainerStrategy.PROPORTION_TO_FRAME, cc.ContentStrategy.SHOW_ALL),);    

       setTimeout(() => {
        Helper.handleResize();
       }, 1);
        UIManager.instance = this;
    },


    mobileView(w){
       
        this.isHActive = false;
        this.hNode.active = false;
        this.vNOde.active = true;

        this.vNOde.width = w;
        this.musicWidget.bottom = 70;
        this.musicWidget.left = -5;

        this.musicButton.scale = 0.6;
        this.musicButton.x = 20;
       
        let ws = this.node.getComponentsInChildren(cc.Widget);
        for(let i in ws){
            ws[i].updateAlignment();
        }
       
        this.vNOde.getComponent('UIBase').sendButton.active = this.game.sendButton.active;
        this.vNOde.getComponent('UIBase').text.string = this.game.editBox.string;
        this.vNOde.getComponent('UIBase').text.enabled = this.game.editBox.enabled;

        this.game.sendButton = this.vNOde.getComponent('UIBase').sendButton;
        this.game.editBox = this.vNOde.getComponent('UIBase').text;

        if(this.game.microButton){
            this.vNOde.getComponent('UIBase').microButton.active = this.game.microButton.active;
        }

        if(this.game.recordButton){
            this.vNOde.getComponent('UIBase').recordButton.active = this.game.recordButton.active;
        }

        this.game.microButton = this.vNOde.getComponent('UIBase').microButton;
        this.game.recordButton = this.vNOde.getComponent('UIBase').recordButton;

        if( UIManager.uiBase){
            UIManager.uiBase = this.vNOde.getComponent('UIBase');
        }
    },

    pcView(w){
       
        this.isHActive = true;
        this.hNode.active = true;
        this.vNOde.active = false;
        this.hNode.width = w;

        this.musicWidget.bottom = 46;
        this.musicWidget.left = 62;
        this.musicWidget.updateAlignment();

        this.musicButton.scale = 1;
        this.musicButton.x = 0;

        let ws = this.node.getComponentsInChildren(cc.Widget);
        for(let i in ws){
            ws[i].updateAlignment();
        }

        this.hNode.getComponent('UIBase').sendButton.active = this.game.sendButton.active;
        this.hNode.getComponent('UIBase').text.string = this.game.editBox.string;
        this.hNode.getComponent('UIBase').text.enabled = this.game.editBox.enabled;

        this.game.sendButton = this.hNode.getComponent('UIBase').sendButton;
        this.game.editBox = this.hNode.getComponent('UIBase').text;

        if(this.game.microButton){
            this.hNode.getComponent('UIBase').microButton.active = this.game.microButton.active;
        }

        if(this.game.recordButton){
            this.hNode.getComponent('UIBase').recordButton.active = this.game.recordButton.active;
        }

        this.game.microButton = this.hNode.getComponent('UIBase').microButton;
        this.game.recordButton = this.hNode.getComponent('UIBase').recordButton;

        if( UIManager.uiBase){
            UIManager.uiBase = this.hNode.getComponent('UIBase');
        }

        this.hNode.getComponent("HUI").adjustMushroom(w);
       
    },

    closeStartBlock() {
        this.startBlock.active = false;
        this.cameraMove.initialize();
        this.game.blockClickHandler();
    },

    isBlockActive(){
       return this.startBlock.active ;
    },

    updateSendButton(active){
        if(!this.isHActive){
            this.vNOde.getComponent("UIBase").updateSendButton(active)
        }
        else{
            this.hNode.getComponent("UIBase").updateSendButton(active)
        }
    },

    toggleDebug: function () {
        return;
        this.debugNode.node.active = !this.debugNode.node.acctive;
    },

    logout() {
        window.location.href = Furwee.LOGOUT_URL; 
    },

    home(){
        window.location.href = Furwee.HOME_URL; 
    },

    education(){
      
    },

    onScroll(evt){
       
        let content = this.game.balloonNode;
        for(let i = 0; i < content.children.length; i++){
            let v = content.children[i].convertToWorldSpaceAR(cc.v2(0,0));
            
            let top = v.y + content.children[i].height/2;
            let bot = v.y - content.children[i].height/2;
          //  console.log(top, bot);
            if(bot >1080 || top < 540){
                content.children[i].children[0].active = false;
               // console.log("hide", i);
            }
            else{
                content.children[i].children[0].active = true;

                bot = Math.max(650, bot);
                content.children[i].opacity =  255 - (bot - 650)/540 * 255;

                
            }
        }

        
    }
});

cc.ScrollView.prototype._calculateBoundary = function() {
    if (this.content) {
        //refresh content size
        let layout = this.content.getComponent(cc.Layout);
        if(layout && layout.enabledInHierarchy) {
            layout.updateLayout();
        }
        let viewSize = this._view.getContentSize();

        let anchorX = viewSize.width * this._view.anchorX;
        let anchorY = viewSize.height * this._view.anchorY;

        this._leftBoundary = -anchorX;
        this._bottomBoundary = -anchorY;

        this._rightBoundary = this._leftBoundary + viewSize.width;
        this._topBoundary = this._bottomBoundary + viewSize.height;

        //this._moveContentToTopLeft(viewSize);
    }
}