let UIManager = require("UIManager");

let UIBase = cc.Class({
    extends: cc.Component,

    properties: {
        sendButton:cc.Node,
        text: cc.EditBox,
        ///recorder

        microButton: cc.Node,
        recordButton: cc.Node,

    },
    

    //mobile
    startRecord() {
        this.microButton.active = false;
        this.recordButton.active = true;

        this.text.enabled = false;

        UIBase.initRecognition(this);

        UIManager.instance.game.music.onMicChange(true);
        
        if(UIBase.useWebRecognition){
            UIBase.startWebRecognition();
        }
        else{
            UIBase.startAPIRecognition();
        }
    },

    stopRecord() {
        this.microButton.active = true;
        this.recordButton.active = false;

        UIManager.instance.game.music.onMicChange(false);

        if(UIBase.useWebRecognition){
           UIBase.stopWebRecognition();
        }
        else{
            UIBase.stopAPIRecognition();
        }
       
        this.text.enabled = true;
        this.updateUIAfterRecord();
    },

    updateUIAfterRecord() {
        this.sendButton.active = (this.text.string.length != 0);
        this.updateSendButton(this.sendButton.active);
    },

    updateSendButton(active) {
        this.microButton.active = !active;
    },

    statics: {
        ui:null,
        useWebRecognition:false,
        recognition:null,
        textRecognition:"",

         //api
         stream: null,
         recorder: null,
         
         gumStream : null,
         //stream from getUserMedia() 
         rec : null,
         //Recorder.js object 
         input : null,
         audioContext:null,

        initRecognition(uibase){
            UIManager.uiBase = uibase;

            window.SpeechRecognition = window.SpeechRecognition
            || window.webkitSpeechRecognition;

            UIBase.useWebRecognition = SpeechRecognition != undefined;
            UIManager.instance.debugNode.string += "useWebRecognition:" +  UIBase.useWebRecognition + "\n";

            UIBase.textRecognition = "";
        },


        startWebRecognition(){
            let finalString = "";
            UIBase.recognition = new SpeechRecognition();
            UIBase.recognition.interimResults = true;
            UIBase.recognition.continuous = true;
            UIBase.recognition.lang = "en-US";
        
            UIManager.instance.debugNode.string += "startWebRecognition 1\n";
           
            UIBase.recognition.onresult = function (event) {

                console.log(event);
                UIManager.instance.debugNode.string += "startWebRecognition 3"+event+"\n";
                let interString = "";
                let autoEnd = false;
                for (let i = event.resultIndex; i < event.results.length; ++i) {
                    // If the result item is Final, add it to Final Transcript, Else add it to Interim transcript
                    if (event.results[i].isFinal) {
                        finalString += event.results[i][0].transcript;
                        UIBase.textRecognition = finalString;
                        autoEnd = true;
                    
                    }
                    else{
                        interString += event.results[i][0].transcript;
                        UIBase.textRecognition = finalString + interString;
                    
                    }
                }
                //////////
                UIManager.uiBase.text.string = UIBase.textRecognition;

                if (UIManager.uiBase.text.enabled) {
                    UIManager.uiBase.updateUIAfterRecord();
                }
                UIManager.instance.debugNode.string += "startWebRecognition 2\n";

                if(autoEnd){
                    UIManager.uiBase.stopRecord();
                    if(UIManager.uiBase.text.string.length != 0){
                        UIManager.instance.game.sendHandler()
                    }
                }
            };

            UIBase.recognition.onerror = function(event) {

                UIManager.instance.debugNode.string += "startWebRecognition error"+event.error+"\n" + event.message+"\n";
                if(event.error == "network"){
                     UIBase.useWebRecognition = false;
                }
            }

            UIBase.recognition.start();
            UIManager.instance.debugNode.string += "startWebRecognition 6\n";
        },

        stopWebRecognition(){
            UIBase.recognition.stop();
        },

        
        //start api recognition
        startAPIRecognition(){
            
            var AudioContext = window.AudioContext || window.webkitAudioContext;

            let constraints  =  {
                audio: true,
                video: false
            } ;

            navigator.mediaDevices.getUserMedia(constraints).then(function(stream) {

                UIBase.audioContext = new AudioContext();

                console.log("getUserMedia() success, stream created, initializing Recorder.js ..."); 
                /* assign to gumStream for later use */
                UIBase.gumStream = stream;
                /* use the stream */
                UIBase.input = UIBase.audioContext.createMediaStreamSource(stream);
                /* Create the Recorder object and configure to record mono sound (1 channel) Recording 2 channels will double the file size */
                UIBase.rec = new Recorder(UIBase.input, {
                    numChannels: 1
                }) 
                //start the recording process 
                UIBase.rec.record()
                console.log("Recording started");
            }).catch(function(err) {
                
            });
        },

        stopAPIRecognition(){

            UIBase.rec.stop(); //stop microphone access 
            UIBase.gumStream.getAudioTracks()[0].stop();
            //create the wav blob and pass it on to createDownloadLink 

            UIBase.rec.exportWAV(function(blob) {

                let xhr = new XMLHttpRequest();
                let requestURL = UIManager.instance.game.URL + "/audio-to-text/" + UIManager.instance.game.URL_SUFFIX;
                xhr.onreadystatechange = function () {
                    if (xhr.readyState == XMLHttpRequest.DONE) {
                        if (xhr.status == 200) {
                            let json = JSON.parse(xhr.responseText);
                            if(json.text != null){
                                /////////////
                                UIManager.uiBase.text.string = json.text;
                                UIManager.uiBase.updateUIAfterRecord();
                            }
                        }
                    }
                }
                //let params = JSON.stringify({ "audio": blob});
                xhr.open('POST', requestURL, true);

                //URL = window.URL || window.webkitURL;
                
                var fd=new FormData();
                fd.append("audio",blob);

                xhr.send(fd);
            });

        
        }

    }
});
