let UIBase = require("UIBase");
let LayerCamerasMove = require("LayerCamerasMove");
let HUI = cc.Class({
    extends: UIBase,
    properties: {
        mushroom:cc.Node,

        showTopButton: cc.Node,
        topUI: cc.Node,
        _isShowTop: false,


    
    },

    statics: {
        MAX_WIDTH: 2200
    },

    onLoad(){
        this.showTopButton.on(cc.Node.EventType.MOUSE_ENTER , this.showTop.bind(this));
       
    },

    onDestroy(){
        this.showTopButton.off(cc.Node.EventType.MOUSE_ENTER , this.showTop.bind(this));
        this.node.off(cc.Node.EventType.TOUCH_END, this.hideTop.bind(this));
    },


    adjustMushroom(w){
        let addWidth = (Math.min(HUI.MAX_WIDTH, w) -1920 )/2;
        this.mushroom.x = Math.max(837, 837 + addWidth);

        LayerCamerasMove.infos[2].x =   this.mushroom.x ;

    },

    hideTop(){
        if(!this._isShowTop){
            return;
        }
        this._isShowTop = false;
        this.topUI.stopAllActions();
        this.topUI.runAction(cc.moveTo(0.3, cc.v2(0,140)));
        this.node.off(cc.Node.EventType.TOUCH_END, this.hideTop.bind(this));
    },

    showTop(){
        if(this._isShowTop){
            return;
        }
        this._isShowTop = true;
        this.topUI.stopAllActions();
        this.topUI.runAction(cc.moveTo(0.3, cc.v2(0,0)));
        this.node.off(cc.Node.EventType.TOUCH_END, this.hideTop.bind(this));
        this.node.on(cc.Node.EventType.TOUCH_END, this.hideTop.bind(this));
    }
});