const {Helper} = require("Helper");
let UIManager = require("UIManager");

let Balloon = cc.Class({
    extends: cc.Component,

    properties: {
        whiteBG: cc.Node,
        pinkBG: cc.Node,
        text: cc.Label,
        stepCount:0
    },

    statics:{
        COLOR_1 : new cc.Color(131,88, 52),
        COLOR_2 : new cc.Color(255, 255, 255),
        index:0
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    onLoad() {
        this.node.on("mobileView", this.mobileView.bind(this));
        this.node.on("pcView", this.pcView.bind(this));
        this.index = Balloon.index;
        Balloon.index ++;
        // this.node.on("mouseOver")
    },

    // update (dt) {},

    lateUpdate() {
        if (this.updateTextHeight >0) {
            this.updateTextHeight --;
            this.node.height = this.bg.height = Math.max(55, this.text.node.height + 20);

            if(this.updateTextHeight == 4){
                this.updateTextHeight = 0;
                 //scroll
                let content = this.node.parent;
                
                let scroll = content.parent.parent.getComponent(cc.ScrollView);
                scroll.scrollToBottom(0);
                this.node.opacity = 255;

                UIManager.instance.onScroll(null);
                }
           
        }
    },

    init(text, isFurwee) {

        if (isFurwee) {
            this.text.node.color = Balloon.COLOR_1;
            this.pinkBG.active = false;
            this.whiteBG.active = true;

            this.bg = this.whiteBG;
        }
        else {
            //this.text.node.color = new cc.Color("#FFFFFF");
            this.whiteBG.active = false;
            this.pinkBG.active = true;

            this.bg = this.pinkBG;
        }
        this.isFurwee = isFurwee;
        this.text.string = text;
        this.updateTextHeight = 6;

        if(!Helper.isPcView){
            this.mobileView(Helper.width);
        }

    },

    step() {
        this.stepCount ++;
        /*if (this.isFurwee) {
            this.node.x = -this.stepCount * 20;
        }
        else {
            this.node.x = this.stepCount * 20;
        }*/

       // this.node.opacity -= 50;

        if(this.node.opacity <=0){
           // this.node.parent.removeChild(this.node);
        }
    },

    mobileView(w){
        this.text.fontSize = 20;
        this.text.lineHeight = 24;
        this.text.string = this.text.string;

        this.node.x = 0;

        this.pinkBG.width = 357;//Math.max(230, w - 200);
        this.whiteBG.width = 357;//Math.max(230, w - 200);
        this.text.node.width = Math.max(190, this.pinkBG.width - 40);

        this.updateTextHeight = Math.max(this.updateTextHeight, 2);
    },

    pcView(){
        this.text.fontSize = 20;
        this.text.lineHeight = 28;
        this.text.string = this.text.string;

       /* if (this.isFurwee) {
            this.node.x = -this.stepCount * 20;
        }
        else {
            this.node.x = this.stepCount * 20;
        }*/

        this.pinkBG.width = 427;//230;
        this.whiteBG.width = 427;//230;
        this.text.node.width = 343;

        this.updateTextHeight = Math.max(this.updateTextHeight, 2);

       /* if(cc.sys.isBrowser){
            this.text.fontSize = 20;
            this.text.lineHeight = 22;
        }*/
    },


    updateOpacity(){
        let content = this.node.parent;
        let y = this.node.y = content.y;
        if( y > 0 && y< 500){
            this.node.active = true;
        }
    }

});
