let Helper = {};

Helper.callBacks = [];
Helper.FIT_HEIGHT = 1080;
Helper.H_MIN_WIDTH = 900;
Helper.MAX_WIDTH = 2200;

Helper.width = 1920;
Helper.isPcView = true;


Helper.initialized = false;
Helper.initialize = function(){
    if(Helper.initialized ){
        return;
    }
    Helper.initialized  = true;
    cc.view.setResizeCallback(Helper.handleResize);
    Helper.handleResize();
}

Helper.handleResize = function(){

    var frameSize = cc.view.getFrameSize();
    let w = Helper.FIT_HEIGHT / frameSize.height * frameSize.width;
        
   

    let isPcView = true;
    if (w < Helper.H_MIN_WIDTH) {
        cc.Canvas.instance.designResolution = new cc.Size(w, Helper.FIT_HEIGHT );
        cc.view.setDesignResolutionSize( cc.Canvas.instance.designResolution.width,  cc.Canvas.instance.designResolution.height, new cc.ResolutionPolicy(cc.ContainerStrategy.PROPORTION_TO_FRAME, cc.ContentStrategy.SHOW_ALL),);
        isPcView = false;
    }
    else{
        w = Math.min(Helper.MAX_WIDTH, w);

        cc.Canvas.instance.designResolution = new cc.Size(w, Helper.FIT_HEIGHT );
        cc.view.setDesignResolutionSize( cc.Canvas.instance.designResolution.width,  cc.Canvas.instance.designResolution.height, new cc.ResolutionPolicy(cc.ContainerStrategy.PROPORTION_TO_FRAME, cc.ContentStrategy.SHOW_ALL),);
        isPcView = true;
    }

    Helper.width = w;
    Helper.isPcView = isPcView;

    for(let i =0 ; i < Helper.callBacks .length; i++){
        Helper.callBacks[i](w, isPcView);
    }
}

Helper.addCallback = function(func){
    Helper.initialize();
    Helper.callBacks.push(func);
}

Helper.removeCallback = function(func){
    let index = Helper.callBacks.indexOf(func);
    if(index != -1){
        Helper.splice(index,1);
    }
}


export {Helper};