let MusicToggle = require("MusicToggle");
let UIManager = require("UIManager");
let LayerCamerasMove = require("LayerCamerasMove");
const { Furwee } = require("Furwee");
let Pet = require("Pet");

let Game = cc.Class({
    extends: cc.Component,

    properties: {

        audioID: -1,

        historyObjects: [],

        pet: Pet,


        balloonPrefab: cc.Prefab,
        balloonNode: cc.Node,

        introSound: {
            type: cc.AudioClip,
            default: null
        },

        initialMsgJSON: null,

        furweeIntialized: false,

        URL: "http://40.121.137.102",
        URL_SUFFIX: "",


        enableTextInput: false,

        uiManager: UIManager,
        cameraMove: LayerCamerasMove,
        music: MusicToggle,
        //ui related
        editBox: cc.EditBox,
        sendButton: cc.Node,


        nextTalks: []


    },

    start() {
    },

    onLoad() {


        this.uiManager.game = this;
        this.cameraMove.game = this;
        this.music.game = this;



        if (!Furwee.DEBUG) {
            this.URL = "/api";
            this.URL_SUFFIX = "";
            Furwee.LOGOUT_URL = "/logout";
            Furwee.HOME_URL = "/";
        }
        else {
            this.URL = "https://furwee.ai/api";
            this.URL_SUFFIX = "";
            Furwee.LOGOUT_URL = "https://furwee.ai/logout";
            Furwee.HOME_URL = "https://furwee.ai/home";
        }

        this.setEnableTextInput(false);
        this.onTextLenChange(this.editBox.string);
        this.startFurwee();
    },

    handleConnect() {
        console.log('connected', this.socket.id);
    },

    startFurwee() {
        this.pet.play();

        let that = this;
        let xhr = new XMLHttpRequest();
        let requestURL = this.URL + "/initial-message/" + this.URL_SUFFIX;

        xhr.onreadystatechange = function () {
            if (xhr.readyState == XMLHttpRequest.DONE) {
                if (xhr.status == 200) {

                    that.initialMsgJSON = JSON.parse(xhr.responseText);
                    if (!that.tryToStartFurweeIntro()) {
                        //preload sound
                        let remoteUrl = that.initialMsgJSON.audio_file_link;
                        cc.loader.load({ url: remoteUrl, type: 'wav' });
                    }
                }
            }
        }
        xhr.open('GET', requestURL, true);
        xhr.setRequestHeader('Content-type', 'application/json');
        xhr.send("");
    },

    tryToStartFurweeIntro() {
        if (this.uiManager.isBlockActive()) {
            return false;
        }
        if (!this.initialMsgJSON) {
            return false;
        }

        if (this.furweeIntialized) {
            return false;
        }

        this.furweeIntialized = true;
        //this.pet.playWelcome();
        this.setEnableTextInput(true);
        //this.pet.playIntro();
        this.onTTSCompleted(this.initialMsgJSON, this.initialMsgJSON.reply);

        this.historyObjects.push({ "index": this.historyObjects.length, "reply": this.initialMsgJSON.reply, "message": this.initialMsgJSON.message });
        return true;
    },


    onTTSCompleted(info, balloonText) {
        if (!info) {
            return;
        }

        this.audioInfo = info.lip_sync_animation;

        let remoteUrl = info.audio_file_link;
        cc.loader.load({ url: remoteUrl, type: 'wav' }, function (err, res) {
            // Use texture to create sprite frame
            this._startPlayTalking(res, balloonText);
        }.bind(this));
    },

    _startTalk(res) {
        this.audioID = cc.audioEngine.play(res);
        if (this.audioID && this.music.shouldbeMuted()) {
            cc.audioEngine.setVolume(this.audioID, 0);
        }
        this.audioOffset = 0;
        this.updateMouth();


        cc.audioEngine.setFinishCallback(this.audioID, this._completeTalk.bind(this));
    },

    _completeTalk() {
        this.audioID = -1;
        this.pet.startIdle();

        if (this.nextTalks.length) {
            let nextTalk = this.nextTalks.splice(0, 1)[0];
            nextTalk.balloonComp.whiteBG.opacity = 255;
            nextTalk.balloonComp.text.node.opacity = 255;
            this._startTalk(nextTalk.audioRes);
        }
    },

    _startPlayTalking(res, balloonText) {

        if (this.pet.isReadyToTalk()) {
            if (this.audioID != -1) {
                let balloon = this.addBallon(balloonText, true).getComponent("Ballon");
                balloon.whiteBG.opacity = 128;
                balloon.text.node.opacity = 128;
                this.nextTalks.push({ balloonComp: balloon, audioRes: res });
            }
            else {
                this._startTalk(res);
                this.addBallon(balloonText, true);
            }
        }
        else {
            //this.pet.playIntro();
            setTimeout(() => {
                this._startPlayTalking(res, balloonText);
            }, 16);
        }

    },

    didReturnHandler() {
        this.sendHandler();
        setTimeout(function () {
            this.editBox.focus();
        }.bind(this), 0);
    },


    sendHandler() {
        if (!this.enableTextInput) {
            return;
        }
        let sendText = this.editBox.string;
        if (sendText.trim().length == 0) {
            return;
        }

        this.pet.resetIdle();

        /* this.socket.emit("tts", sendText);
         return;
    */

        this.setEnableTextInput(false);
        this.addBallon(sendText, false);
        this.editBox.string = "";
        if (!cc.sys.isMobile) {
            this.editBox.focus();
        } else {
            this.editBox.blur();
        }

        this.onTextLenChange(this.editBox.string);

        let that = this;
        let xhr = new XMLHttpRequest();

        let requestURL = this.URL + "/messages/" + this.URL_SUFFIX;
        xhr.onreadystatechange = function () {
            if (xhr.readyState == XMLHttpRequest.DONE) {
                if (xhr.status == 200) {
                    let json = JSON.parse(xhr.responseText);
                    that.setEnableTextInput(true);
                    that.onTTSCompleted(json, json.reply);

                    that.historyObjects.push({ "index": that.historyObjects.length, "reply": json.reply, "message": json.message });
                }
            }
        }
        let params = JSON.stringify({ "message": sendText, history: this.historyObjects });
        xhr.open('POST', requestURL, true);
        xhr.setRequestHeader('Content-type', 'application/json');
        xhr.send(params);


        let xhr2 = new XMLHttpRequest();
        let requestURL2 = this.URL + "/emotion/" + this.URL_SUFFIX;
        xhr2.onreadystatechange = function () {
            if (xhr2.readyState == XMLHttpRequest.DONE) {
                if (xhr2.status == 200) {
                    console.log(xhr2.responseText);
                    let response = JSON.parse(xhr2.responseText);
                    that.pet.setEmotion(response.emotion);

                }
            }
        }
        let params2 = JSON.stringify({ "message": sendText });
        xhr2.open('POST', requestURL2, true);
        xhr2.setRequestHeader('Content-type', 'application/json');
        xhr2.send(params2);
    },

    updateMouth() {
        //audio_offset_ms: 1162.5, viseme_id: 11}
        let id = this.audioInfo[this.audioOffset].viseme_id;
        this.pet.updateMouth(id);
    },

    update(dt) {
        if (this.audioID != -1) {
            let time = cc.audioEngine.getCurrentTime(this.audioID)

            while (this.audioInfo.length > this.audioOffset
                && (this.audioInfo[this.audioOffset].audio_offset_ms < time * 1000 + 15)) {
                // && ((this.audioOffset + 1 >= this.audioInfo.length) || (Math.abs(this.audioInfo[this.audioOffset ].audioOffset - time* 1000 ) > (this.audioInfo[this.audioOffset +1].audioOffset - time* 1000 )))){
                this.updateMouth();
                this.audioOffset += 1;

            }
        }
    },

    onTextLenChange(textContent) {
        this.sendButton.active = textContent.length != 0;
        this.uiManager.updateSendButton(this.sendButton.active);
    },

    addBallon(message, isFurwee) {
        for (var i = 0; i < this.balloonNode.children.length; ++i) {
            this.balloonNode.children[i].getComponent("Ballon").step();
        }

        let balloon = cc.instantiate(this.balloonPrefab);
        balloon.getComponent('Ballon').init(message, isFurwee);
        balloon.opacity = 0;
        this.balloonNode.addChild(balloon);
        return balloon;

        // this.balloonNode.parent.parent.getComponent(cc.ScrollView).stopAutoScroll();
    },

    blockClickHandler() {
        this.music.initialize();
        this.tryToStartFurweeIntro();
        this.pet.playWelcome();
    },

    errorClickHandler() {
        if (window.captureError) {
            window.captureError();
        }

    },

    setEnableTextInput(bool) {
        this.enableTextInput = bool;
    }
});
